p {
  display: block;
  margin-block-start: 0;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}

div{
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.magicBTNui{
  background-color: #282c34;
  width: 120px;
  height: 40px;
  font-weight: 700;
  border-radius: 12px;
  padding: 9px;
  margin: auto;
  margin-right: 16px;
  cursor: pointer;
}

.dltBTN{
  cursor: pointer;
  margin: auto;
  margin-right: 16px;
  padding: 9px;
}

.name-bold{
  display: flex;
  background: black;
  padding-left: 1rem;
  color : white;
  font-size : 14px;

}

.list{
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid #e3e5e8;
}

.list>div>h4, .list>div>span{
  margin: 16px 0 16px 0;
}

.modal{
  background: #d0d0d0;
  color: #fff;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
